@import url(https://fonts.googleapis.com/css?family=Roboto:400,300,100,500,700,900);
.contact-form {
  margin-top: 30px;
}
body {
  /* background: url("../../../public/assets/images/hdBackground.jpg"); */
  /* background-size: cover; */
  /* background-repeat: no-repeat; */
  /* height: 100vh; */
  background: #000;
}

/* .contact-wrap {
  z-index: 1;
} */
.contact-wrap {
  margin: auto;
}
.contact-form label {
  color: #fff;
  margin-bottom: 1rem;
  font-size: 20px;
  text-transform: uppercase;
  letter-spacing: 1px;
  line-height: 36px;
}
.category-dropdown {
  width: 100%;
  height: 65px;
  text-align: center;
  font-size: 22px;
  cursor: pointer;
}
.category-dropdown .category-name {
  padding: 100px !important;
}

.contact-form .input-block {
  background: white;
  border: solid 3px #ff512f;
  width: 100%;
  height: 40px;
  padding: 30px;
  position: relative;
  margin-bottom: 20px;
  -moz-transition: all 0.3s ease-out;
  -o-transition: all 0.3s ease-out;
  -webkit-transition: all 0.3s ease-out;
  transition: all 0.3s ease-out;
}

.contact-form .input-block.focus {
  background-color: #fff;
  border: solid 1px #fb2900;
}

.contact-form .input-block.textarea {
  height: auto;
}

.contact-form .input-block.textarea .form-control {
  height: auto;
  resize: none;
}
.contact-form input {
  background-color: white !important;
}
.contact-form .input-block label {
  padding: 0 10px;
  background: #fff;
  position: absolute;
  left: 8px;
  top: 20px;
  display: block;
  margin: 0;
  font-weight: 300;
  z-index: 1;
  color: #999;
  font-size: 18px;
  line-height: 10px;
}

.contact-form .input-block .form-control {
  background-color: transparent;
  border: medium none;
  border-radius: 0;
  box-shadow: none;
  color: #333;
  font-size: 18px;
  height: 40px;
  padding: 0;
  position: relative;
  top: -20px;
  z-index: 2;
}

.contact-form .input-block .form-control:focus label {
  top: 0;
}

.contact-form .square-button {
  background-image: url("data:image/svg+xml;base64,");
  background-size: 100%;
  background-image: -webkit-gradient(linear, 0% 50%, 100% 50%, color-stop(0%, #ff512f), color-stop(100%, #dd2476));
  background-image: -moz-linear-gradient(left, #ff512f, #dd2476);
  background-image: -webkit-linear-gradient(left, #ff512f, #dd2476);
  background-image: linear-gradient(to right, #ff512f, #dd2476);
  color: #fff;
  font-size: 26px;
  text-transform: uppercase;
  font-weight: 700;
  text-align: center;
  -moz-border-radius: 2px;
  -webkit-border-radius: 2px;
  border-radius: 2px;
  -moz-transition: all 0.3s ease;
  -o-transition: all 0.3s ease;
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
  padding: 0 60px;
  height: 60px;
  border: none;
  width: 100%;
}

.contact-form .square-button:hover,
.contact-form .square-button:focus {
  background-color: white;
}

@media (min-width: 768px) {
  .contact-wrap {
    width: 60%;
    margin: auto;
  }
}
/*----page styles---*/
.contact-wrap {
  padding: 15px;
}
.large-cta-section {
  background: #1e9fc4 linear-gradient(45deg, #3e3ed0, #1e9fc4 57%, #4fddd5) repeat scroll 0 0;
  box-shadow: 0 6px 12px -4px rgba(54, 56, 66, 0.25) inset;
  text-align: center;
  z-index: 1;
  height: 400px;
  position: relative;
  overflow: hidden;
}
.medium-large-wrapper {
  padding-top: 50px;
}
.h2.large {
  display: inline-block;
  font-size: 5em;
  font-family: "Roboto", sans-serif;
}
.white {
  color: #fff;
}
.h2 > .yellow {
  color: #f5ee33;
  font-weight: 600;
}
.h3 {
  font-family: "Roboto", sans-serif;
  font-size: 1.4em;
  font-weight: 300;
  line-height: 1.8rem;
  margin-top: 10px;
  margin-bottom: 40px;
}

.footer-cta-link::after {
  background-image: url("https://neverbounce.com/images/background-images/yellow-airplane-left.png");
  background-repeat: no-repeat;
  background-size: contain;
  bottom: -10em;
  content: "";
  display: block;
  height: 10em;
  left: -8.5em;
  pointer-events: none;
  position: absolute;
  width: 7em;
}
.footer-cta-link {
  position: relative;
}
.cta-link {
  background: #fff none repeat scroll 0 0;
  border-radius: 4px;
  color: #1e9fc4;
  letter-spacing: 2px;
  line-height: 1rem;
  margin-left: 1em;
  margin-right: 1em;
  margin-top: 4em;
  padding: 0.88rem 1.5em;
  text-decoration: none;
  text-transform: uppercase;
  transition: background 0.2s ease-in-out 0s;
  vertical-align: middle;
}
a.cta-link:hover {
  background: #f5ee33;
  text-decoration: none;
}
.cta-purple-email {
  left: 0;
  position: absolute;
  top: 5em;
  z-index: 0;
  transform: translate3d(0px, 0%, 0px);
}
.cta-blue-email {
  bottom: -45px;
  position: absolute;
  right: 15%;
  width: 15em;
  z-index: 0;
  transform: translate3d(86.875px, -8.6875%, 0px);
}

.cta-left-line {
  left: 0;
  opacity: 0.7;
  pointer-events: none;
  position: absolute;
  top: -38em;
  z-index: 0;
}
.cta-teal-airplane {
  opacity: 0.8;
  pointer-events: none;
  position: absolute;
  right: 10px;
  top: 3em;
  width: 15em;
  z-index: 4;
}
.made-with-love {
  margin-top: 40px;
  padding: 10px;
  clear: left;
  text-align: center;
  font-size: 12px;
  font-family: arial;
  color: #666;
}

.made-with-love i {
  font-style: normal;
  color: #f50057;
  font-size: 14px;
  position: relative;
  top: 2px;
}

.made-with-love a {
  color: #666;
  text-decoration: none;
}

.made-with-love a:hover {
  text-decoration: underline;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type="number"] {
  -moz-appearance: textfield;
}
